@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Global font improvements */
* {
  font-family: 'Montserrat', sans-serif;
}

/* Custom font weight utilities for Montserrat */
.font-montserrat {
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Smooth theme transitions - matching gfe-sphere */
html {
  @apply transition-colors duration-300 ease-in-out;
}

body {
  @apply transition-colors duration-300 ease-in-out;
}

/* Smooth transitions for all elements */
* {
  @apply transition-colors duration-200 ease-in-out;
}

/* Exclude certain elements from transitions for performance */
svg,
img,
canvas,
video {
  transition: none !important;
}

/* Control Module Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fadeIn {
  animation: fadeIn 0.3s ease-in both;
}

@keyframes slideDown {
  from {
    opacity: 0;
    max-height: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    max-height: 1000px;
    transform: translateY(0);
  }
}

.animate-slideDown {
  animation: slideDown 0.3s ease-out both;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

@keyframes shimmer {
  0% {
    background-position: -1000px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}

.loading-skeleton {
  background: linear-gradient(
    90deg,
    #f0f0f0 25%,
    #e0e0e0 50%,
    #f0f0f0 75%
  );
  background-size: 1000px 100%;
  animation: shimmer 2s infinite;
}

/* Custom scrollbar for alarms panel */
.scrollbar-thin {
  scrollbar-width: thin;
}

.scrollbar-thin::-webkit-scrollbar {
  width: 6px;
}

.scrollbar-thin::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.scrollbar-thin::-webkit-scrollbar-thumb {
  background: #cbd5e0;
  border-radius: 10px;
}

.scrollbar-thin::-webkit-scrollbar-thumb:hover {
  background: #a0aec0;
}

/* Button Styles - Matching gfe-sphere exactly */
@layer components {
  .btn-primary {
    @apply bg-gradient-to-r from-[#8c7ae6] to-[#a78bfa] hover:opacity-90 text-white font-medium py-2.5 px-6 rounded-lg transition-opacity duration-200 shadow-sm hover:shadow-md;
  }

  .btn-secondary {
    @apply border-2 border-[#8c7ae6] text-[#8c7ae6] hover:bg-[#8c7ae6] hover:text-white transition-all font-medium py-2.5 px-6 rounded-lg bg-transparent;
  }

  .btn-outline {
    @apply border border-gray-300 dark:border-gray-700 text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-800 bg-white dark:bg-gray-800 font-medium py-2.5 px-4 rounded-lg transition-all;
  }

  .input-field {
    @apply w-full px-3 py-2.5 border border-gray-300 dark:border-gray-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8c7ae6] dark:focus:ring-[#a78bfa] focus:border-transparent transition-all bg-white dark:bg-gray-900 text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-gray-500;
  }

  .dropdown-button {
    @apply w-full px-2.5 py-2 border border-gray-300 dark:border-gray-700 rounded-md focus:outline-none focus:ring-2 focus:ring-gray-500 dark:focus:ring-gray-400 focus:border-transparent transition-all bg-white dark:bg-gray-800 text-gray-900 dark:text-white cursor-pointer disabled:bg-gray-100 dark:disabled:bg-gray-700 disabled:cursor-not-allowed disabled:text-gray-500 dark:disabled:text-gray-400 min-h-[38px] flex items-center justify-between text-left text-sm;
  }

  .dropdown-menu {
    @apply fixed z-[9999] bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg shadow-xl overflow-hidden;
  }
}

/* Custom scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-100 dark:bg-gray-900;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-300 dark:bg-gray-700 rounded-full;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-gray-400 dark:bg-gray-600;
}

/* Smooth dark mode transitions for root element */
:root {
  color-scheme: light;
}

:root.dark {
  color-scheme: dark;
}

/* Hide scrollbar for tabs */
.hide-scrollbar {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
}

.hide-scrollbar::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
} 